import React from "react";
import { useState } from "react";
import { useSelector } from "react-redux"

export default function Table(props){
    const data = useSelector((state) => state.data);
    const uploadedFiles = useSelector((state) => state.uploadedFiles);

    // modal
    const [isModalOpen, setIsModalOpen] = useState(false);
    const [selectedFile, setSelectedFile] = useState(null);
    const [selectedFileID, setSelectedFileID] = useState(null);

    const openModal = (file, id) => {
        setSelectedFile(file);
        setSelectedFileID(id)
        setIsModalOpen(true);
    }

    const closeModal = () => {
        setIsModalOpen(false);
        setSelectedFile(null);
        setSelectedFileID(null);
    }

  return (
    <div>
        {/* Table */}
            <div className="tableContainer table-text-size">
            {data.map((tableData, i) => (
                <div className="data-table-wrapper" key={i}>
                    <button className="modal-btn" onClick={() => openModal(uploadedFiles[i], i)}>
                        Open {uploadedFiles[i]?.name} in modal
                    </button>
                    <div className="data-table">
                        <table>
                            <thead>
                                <tr>
                                    {tableData[0].map((header, index) => (
                                        <th key={index}>{header}</th>
                                    ))}
                                </tr>
                            </thead>
                            <tbody>
                                {tableData.slice(1).map((row, j) => (
                                    <tr key={j}>
                                        {row.map((value, k) => (
                                            <td key={k}>{value}</td>
                                        ))}
                                    </tr>
                                ))}
                            </tbody>
                        </table>
                    </div>
                </div>
            ))}
            </div>

            {isModalOpen && (
            <div className="modal">
            <div className="modal-content">
                <span className="close-button" onClick={closeModal}>
                    &times;
                </span>

                <div className="selectedTableContainer table-text-size">
                    <h3>{selectedFile && <p>{selectedFile.name}</p>}</h3>
                        <div className="selectedTable">
                            <table>
                                <thead>
                                    <tr>
                                        {data[selectedFileID][0].map((header, index) => (
                                            <th key={index}>{header}</th>
                                        ))}
                                    </tr>
                                </thead>
                                <tbody>
                                    {data[selectedFileID].slice(1).map((row, j) => (
                                        <tr key={j}>
                                            {row.map((value, k) => (
                                                <td key={k}>{value}</td>
                                            ))}
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                </div>
            </div>
            </div>
            )}
    </div>
  );
};